.SkyBlock {
    width: 100%;
    height: 1100px;
    top: 0px;
    z-index: -1;
}

.IntroBlock {
    position: fixed;
    width: 100%;
    top: 100px;
    z-index: 0;
}

.Mountains {
    width: 100%;
    height: 600px;
    position: absolute;
    background-size: cover;
    background-position: center;
    top: 510px;
    z-index: 2;
}

.WorkBlock {
    position: relative;
    width: 100%;
    background: rgb(4, 25, 56);
    color: white;
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
    z-index: 1;
}

.SchoolBlock {
    position: relative;
    width: 100%;
    background: #fff4d1;
    color: rgb(4, 25, 56);
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
    z-index: 1;
}

.TitleText {
    font-family: Pixelated;
    font-size: 100px;
    margin-left: 70px;
    margin-bottom: 10px;
}

.TitleTextSub {
    font-family: Pixelated;
    font-size: 50px;
    padding-left: 70px;
    padding-bottom: 5px;
}

.Links {
    height: 360px;
    width: 40px;
    background-size: cover;
    background-position: center;
}

.LinkTexts {
    margin-right: 70px;
    list-style-type: none;
    text-decoration: none;
}

.LinkLine {
    display: flex;
    align-items: center;
    justify-content: end;
}

.LinkLineElement {
    display: inline-block;
}

.LinkTexts li {
    font-family: Pixelated;
    font-size: 40px;
    text-align: right;
}

.LinkTexts a {
    text-decoration: none;
}

.ResumeTitle {
    font-family: Pixelated;
    font-size: 70px;
    padding-top: 100px;
    padding-bottom: 50px;
}

.ResumeHeader {
    font-family: Pixelated;
    font-size: 45px;
    text-align: left;
    color: #f2ac29;
}

.ResumeLeft {
    font-family: Pixelated;
    font-size: 35px;
    text-align: left;
    color: #fff4d1;
    margin-bottom: 15px;
}

.ResumeRight {
    font-family: Pixelated;
    font-size: 35px;
    text-align: right;
    color: #fff4d1;
    margin-bottom: 15px;
}

.ResumeText {
    font-family: OpenSans;
    font-size: 25px;
    text-align: left;
}

.SchoolHeader {
    font-family: Pixelated;
    font-size: 45px;
    text-align: left;
    color: #f2ac29;
}

.SchoolDegree {
    font-family: Pixelated;
    font-size: 35px;
    text-align: left;
    color: rgb(100, 195, 250);
}

.SchoolName {
    font-family: Pixelated;
    font-size: 25px;
    text-align: left;
    color: rgb(4, 25, 56);
    margin-bottom: 25px;
}

